body {
    background-image: radial-gradient(rgb(49, 49, 49), rgb(39, 39, 39));/*rgb(246, 246, 255);*/
    background-size: contain;
    background-repeat: no-repeat;
    height: 100vh;
    overflow-x: hidden;
}

@media screen and (min-width: 684px) {
    p {
        color: rgb(167, 167, 167);
        text-align: left !important;
    }
    
    span {
        position: relative;
    }
    
    .no-select {
        user-select: none;
    }
    
    .App {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translateY(-50%) translateX(-50%);
        max-width: 150rem;
        text-align: center;
    }
    
    
    .footerBar {
        position: absolute;
        background: black;
        width: 100%;
        height: 5rem;
    }

    button {
        margin-top: .5rem;
        position: absolute;
        padding: 1rem;
        left: 50%;
        transform: translateX(-50%);
        background: none;
        border: none;
        outline: none;
        cursor: pointer;
        transition: .1s all;
    }
    
    button:before {
        content: '';
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
    
    .left {
        margin-left: -5rem;
    }
    
    .right {
        margin-left: 5rem;
    }
    
    .not-clickable {
        cursor: default !important;
        opacity: .5;
    }
    
    .donations-container {
        position: absolute;
        display: block;
        top: 2.5rem;
        left: 15%;
        transform: translateY(-50%);
        max-width: 15rem;
        background: rgb(255, 0, 0);
        cursor: pointer;
        padding: 1rem;
        transition: .3s all;
    }
    
    .donations-container:hover{
        box-shadow: 0 0 10px rgba(255, 0, 0, 0.397);
        padding: 1.1rem;
    }
    
    .donations-button {
        color: white;
        max-width: 5rem;
    }

    .contact-container {
        box-shadow: 0 0 10px rgba(255, 0, 0, 0.397);
        padding: 1.1rem;
    }

    .contact-container {
        position: absolute;
        display: block;
        top: 2.5rem;
        right: 15%;
        transform: translateY(-50%);
        max-width: 15rem;
        cursor: pointer;
        padding: 1rem;
        transition: .3s all;
    }

    .contact-button {
        color: white;
        max-width: 5rem;
    }
}

